.main {
  display: grid;
  grid-area: body;
  grid-template-columns: 40% 60%;
  padding: 30px 80px;
  background-color: var(--main-bg-color);
}

.title {
  font-size: 44px;
  margin-bottom: 30px;
}

.button {
  margin-right: 30px;
}

.learn {
  position: relative;
  background: url('/public/icons/main.svg') no-repeat center / contain;
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.learn__link {
  display: block;
  width: 100px;
  height: 100px;
  position: relative;
  border-radius: 100%;
  border: 3px solid var(--link-color);
  color: var(--link-color);
  text-decoration: none;
  transition: all 0.3s;
}

.learn__link:hover {
  box-shadow: 0px 0px 54px 15px rgba(87, 27, 122, 0.2);
}

.learn__title {
  position: absolute;
  bottom: -30px;
  left: 50%;
  transform: translateX(-50%);
}

.book {
  position: absolute;
  bottom: 30px;
  background: url('/public/icons/book.svg') no-repeat center / contain;
}

.audio {
  position: absolute;
  bottom: 50%;
  left: 10%;
  background: url('/public/icons/audio.svg') no-repeat center / contain;
}

.sprint {
  position: absolute;
  top: 0;
  left: 20%;
  background: url('/public/icons/sprint.svg') no-repeat center / contain;
}
